
import { defineComponent } from "vue";
import AppStatus from "@/components/AppStatus.vue";
import AppTabs from "@/components/AppTabs.vue";
import AppSelectSingle from "@/components/AppSelectSingle.vue";
import AppSelectMulti from "@/components/AppSelectMulti.vue";

export default defineComponent({
  components: {
    AppStatus,
    AppTabs,
    AppSelectSingle,
    AppSelectMulti,
  },
  data() {
    const buttons = [];
    for (const design of ["normal", "circle", "small"]) {
      for (const color of ["primary", "secondary"]) {
        const row = [];
        for (const [text, icon] of [
          ["Text", ""],
          ["Text", "download"],
          ["", "download"],
        ]) {
          row.push([design, color, text, icon]);
        }
        buttons.push(row);
      }
    }

    return {
      buttons,
      singleSelectOptions: [
        "apple",
        "banana",
        "cherry",
        "durian",
        "elderberry",
        "fig",
        "grape",
        "honeydew",
      ],
      singleSelectValue: "durian",
      multiSelectOptions: [
        { value: "fruits", count: 0 },
        { value: "vegetables", count: 7 },
        { value: "colors", count: 42 },
        { value: "animals", count: 999 },
        { value: "cars" },
        { value: "schools" },
        { value: "appliances" },
      ],
      multiSelectValue: [{ value: "vegetables" }],
    };
  },
  methods: {
    alert() {
      window.alert("alert");
    },
  },
});
